html{
  min-height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: url(/static/media/mario_bck.3ecd0557.jpg) no-repeat;
  background-size: cover;
  background-position: bottom;
  min-height: 100%;
}

form{
  padding: 20px;
  margin-top: 60px;
}

form button, form h5{
  margin: 20px 0;
}


.toDoContainer{
  background-color: rgba(0,0,128, 0.2);
  overflow:auto;
  min-height: 500px !important;
  max-height: 500px;
  width: 30%;
  margin-top: 20px;
  border-radius: 5px;

}

.draggingContainer{
  display: flex;
  flex-direction: column;
  min-height: 412px !important;
  overflow: auto;
}

.task{
  margin: 20px 5px;
  border-radius: 5px;
}

.column-main-area{
  display: flex ;
  justify-content: space-between !important;
}

.column-title{
  margin: 0 !important;
} 

.notifi{
  border-radius: 10px;
  margin-top: 20px
}

.title-header{
  display: flex;
  justify-content: space-between;
}

::-webkit-scrollbar-track{
	box-shadow: inset 0 0 6px #d10945;
    background-color: #F5F5F5;
    border-radius: 5px;}
::-webkit-scrollbar{width: 7px;}
::-webkit-scrollbar-thumb{
    background-color: #d10945;
    border-radius: 5px;}

  .form-edit{
      padding: 0;
      margin: 0;
    }
.component-hiden{
    opacity:0;
}

